  inline-size: 0;
  block-size: 0;
  border-style: solid;
  border-width: var(--v-space-5xs) var(--v-space-5xs) 0 var(--v-space-5xs);
  border-color: var(--brand-primary-accent) transparent transparent transparent;
}
