/**
 * @file
 * Nav Primary.
 *
 * This is a refactored version of the primary navigation styles, inspired by
 * Drupal's Olivero theme. It incorporates modern CSS practices and integrates
 * with the Mukurtu V4 theme variables.
 * To find menu grid placement, see _page.scss.
 */
/**
 * @file
 * Nav Primary.
 */
.primary-nav__menu {
  flex-wrap: wrap;
  list-style: none;
}

.primary-nav__menu-item {
  margin-block-end: var(--v-space-5xs);
  list-style: none;
}
.primary-nav__menu-item.primary-nav__menu-item--has-children {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.primary-nav__menu-item.primary-nav__menu-item--has-children .primary-nav__menu-link--link, .primary-nav__menu-item.primary-nav__menu-item--has-children .primary-nav__menu-link--nolink {
  /* Ensure that long text doesn't make the mobile expand button wrap. */
  flex-basis: calc(100% - var(--v-space-s));
}
.primary-nav__menu-item.primary-nav__menu-item--has-children .primary-nav__menu-link--nolink {
  line-height: 100%;
}

.primary-nav__menu-link {
  flex-grow: 1;
  color: var(--color-text-neutral-loud);
  font-size: var(--font-size-l);
  text-decoration: none;
  padding-inline: var(--v-space-3xs);
  padding-block: var(--v-space-5xs);
  border-block-end: 4px solid;
  border-block-end-color: var(--color-transparent);
  transition: color 0.12s ease-in, border-block-end-color 0.12s ease-in;
}
.primary-nav__menu-link--has-children {
  padding-inline-end: var(--v-space-6xs);
}
.primary-nav__menu-link:hover {
  border-block-end-color: var(--brand-secondary-dark);
  color: inherit;
}
.primary-nav__menu-link:hover, .primary-nav__menu-link:active {
  color: var(--brand-secondary-dark);
}
.primary-nav__menu-link:focus {
  outline: auto 2px var(--brand-secondary-dark);
  outline-offset: 2px;
  color: var(--brand-secondary-dark);
}
.primary-nav__menu-link--nolink, .primary-nav__menu-link--button, .primary-nav__menu-link--has-children {
  /* stylelint-disable-next-line no-descending-specificity */
}
.primary-nav__menu-link--nolink:hover, .primary-nav__menu-link--button:hover, .primary-nav__menu-link--has-children:hover {
  border-block-end-color: transparent;
}
.primary-nav__menu-link.is-active {
  border-block-end-color: var(--brand-secondary-accent);
}
.primary-nav__menu-link.is-active:hover {
  border-block-end-color: var(--brand-secondary-dark);
}

/* stylelint-disable-next-line no-descending-specificity */
.primary-nav__menu-link--button {
  position: relative;
  cursor: pointer;
  text-align: start;
  border: 0;
  background: transparent;
  font-weight: normal;
  /* Plus icon for mobile navigation. */
}
.primary-nav__menu-link--button:hover, .primary-nav__menu-link--button:focus, .primary-nav__menu-link--button:active {
  background-color: transparent;
}
.primary-nav__menu-link--button.primary-nav__menu-link--has-children {
  padding-inline-end: var(--v-space-s); /* Ensure text does not overlap icon. */
}
.primary-nav__menu-link--button.primary-nav__menu-link--has-children::before, .primary-nav__menu-link--button.primary-nav__menu-link--has-children::after {
  position: absolute;
  inset-inline-end: 9px;
  inset-block-start: calc(var(--v-space-5xs) + 17px); /* Visually align button with menu link text. */
  inline-size: 18px;
  block-size: 0;
  content: "";
  /* Intentionally not using CSS logical properties. */
  /*
   * Using --brand-primary as --color--primary-50 is not defined in the
   * theme.
   */
  border-block-start: solid 3px var(--brand-primary);
}
.primary-nav__menu-link--button.primary-nav__menu-link--has-children::after {
  transition: opacity 0.2s;
  transform: rotate(90deg);
}
.primary-nav__menu-link--button.primary-nav__menu-link--has-children[aria-expanded=true]::after {
  opacity: 0;
}

.primary-nav__menu-link-inner {
  position: relative;
  display: inline-flex;
